            <loopPieNew :params="peoplePieParams" @change="poprsShow"></loopPieNew>
          </div>
        </div>
        <div class='c-b-left'>
          <ctitle :title="'信访群体'"></ctitle>
          <div class="charts">
            <barline :params="rqnlBarParams"></barline>
