        </div>
        <div class="c-b-left">
          <ctitle :title="'信访诉求'"></ctitle>
          <div class="charts chart-pie">
            <pie-chart3D :params="xfmdPieParams"></pie-chart3D>
          </div>
          <p>信访问题分布</p>
        </div>
        <div class='c-b-left'>
          <div class="charts">
            <loopPie :params="tswtPieParams"></loopPie>
          </div>
          <p>投诉问题分布</p>
        </div>
      </div>
    </div>
    <!--数据反查弹框-->
    <popLists v-if="popLists" :data="popLists" ref="popLists"></popLists>
  </div>
  </div>
</template>

