  import ctitle from "@/components/title/index";
  // import barlineY from "@/components/bar/barLine2";
  import barline from "@/components/bar/BarLine";
  import LinesR from "@/components/line/LinesR1";
  import loopPieNew from "@/components/pie/LoopPie1";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import popLists from "@/components/popLists/index";
  
  export default {
    name: "jjf",
    components: {
      ctitle,
      barline,
      LinesR,
      loopPie,
      PieChart3D,
      popLists,
      loopPieNew
      // barlineY
    },
    data(){
      return{
        // 区划分布-返回按钮
        countyBackShow:false,
        // 1.区划排名
        xjName:[],
        countyBackShow:false,
        curCity: "广东省",
        shijCity: "",
        xjCity: "",
        shej_02: "广东省",
        shij_02: "invalid",
        xj_02: "invalid",
        nameP:'invalid',
        xflphBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "10%",
            right: "10%",
            top: "5%",
            bottom: "5%",
            barLabelOptions: "top",
            barWidth: "25%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        // 2.进京访信访量人数规模分布走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
            },
            {
              line: "#1a82f7",
            },
          ],
          option: {
            showSymbol: true,
            splitNumber: 4,
            splitXLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            // axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "11%",
            right: "15%",
            top: "19%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            value_g0_1: "去年",
            value_g1_1: "今年"
          },
          xName: "name"
        },
        // 3.重复访次数分布
        dqfbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            borderColor: '#050e27',
						borderWidth: 5,
						minAngle: 15,
            // colors: this.$STANDARD.pieMoreColors,
            colors: this.$STANDARD.gradientColors,
            // radius: ["30%", "65%"],
            radius: ["40%", "55%"],
            center: ["45%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: true
            },
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 5,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true,
          }
        },
        // 4.人数规模分布
        peoplePieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            borderColor: '#050e27',
						borderWidth: 7,
						minAngle: 15,
            colors: ['#ffc833', '#eb9002', '#1ba1ff', '#4eed99', '#13f0ff'],
            // radius: ["30%", "65%"],
            radius: ["35%", "51%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: true
            },
            bRichPadding: [30, -50, 0, -50],
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true,
          }
        },
        // 5.信访群体
        rqnlBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "95%"
          },
          options: {
            yAxisColor: '#cdefff',
            xName: "name", //x轴字段
            axisValueShow: true,
            axis1Name:"",
            axisLabelSize: "12%", // label字号
            left: "10%",
            right: "10%",
            top: "10%",
            bottom: "15%",
            barLabelOptions: "top",
            // barWidth: 30,
            barWidth: "45%", // 每个柱形图宽
            splitLineShow: false,
            barLabelShow: true, //柱状图数量值显示
            // axisLabelColor: "rgba(0, 176, 255, .8)",
            // itemLabelColor: "rgba(0, 176, 255, .8)",
            // barGap: '30%',// 柱间距离，
            colors: {
              bar: [["#D5FA9E", "#9ADE72"]]
            },
            map: {
              value: "人数"
            },
          }
        },
        // 6.信访诉求
        xfmdPieParams: {
          data: [],
          angle: 30,
          radius: "28%",
          labelRadius: 30,
          depth3D: 25,
          fontSize: this.fontSize(0.16),
          color: ['#d93e75', '#ffc833', '#4eed99', '#13f0ff', '#1ba1ff'],
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 7.投诉问题分布
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: ['#4eed99', '#d93e75', '#ccbe51', '#eb9002', '#1ba1ff', '#563ad2', '#f89e67', '#13f0ff'],
            radius: ["32%", "40%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.15),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.2)
            },
            length: 20,
            length2: 50,
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 2,
            borderColor: "#050e27",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "10%" //文字大小
            },
            labelSize: "22%", // label字号
            threeLevel:true,
            rollFlag: true
          }
        },
        // 数据反查
        popLists:null,
      }
    },
    mounted() {
      this.init()
    },
    methods:{
      // 初始化组件
      init(){
        this.getColumnCharts(this.date,this.curTypeId);
        this.getAPI(this.date)
      },
      // 数据反查弹框-重复访次数分布
      popShow(obj) {
        let id = obj.id;
        // let id = obj.name;
        if (id) {
          get_http("popLists", {
            'value_id': id,
            'name': obj.name,
          })
          .then(res => {
            this.popLists = res.data;
            this.$nextTick(() => {
              this.$refs.popLists.popListsShow = true;
            })
          })
          .catch(err => {
            console.log(err);
          });
        }
      },
      // 数据反查弹框-人数规模分布
      poprsShow(obj) {
        let id = obj.id;
        // let id = obj.name;
        if (id) {
          get_http("poprsLists", {
            'value_id': id,
            'name': obj.name,
          })
          .then(res => {
            this.popLists = res.data;
            this.$nextTick(() => {
              this.$refs.popLists.popListsShow = true;
            })
          })
          .catch(err => {
            console.log(err);
          });
        }
      },
      // 点击左侧柱图
      changeType(id) {
        this.curTypeId = id;
        this.getColumnCharts(this.curTypeId);
      },
      // 1.区划分布
      getColumnCharts(date,curTypeId) {
        // if (this.curCity === '广东省') {
        //   this.shej_02 = this.curCity;
        //   this.shij_02 = 'invalid';
        //   this.xj_02 = 'invalid';
        //   this.nameP = 'shij_02';
        // } else {
        //   // 市级
        //   if (this.shijCity) {
        //     this.shej_02 = "广东省";
        //     this.shij_02 = this.shijCity;
        //     this.nameP = "xj_02";
        //     if (this.xj_02 !== 'invalid') {
        //       this.shej_02 = "广东省";
        //       this.shij_02 = this.shijCity;
        //       this.xj_02 = this.curCity;
        //     } else {
        //       this.shej_02 = "广东省";
        //       this.shij_02 = this.shijCity;
        //       this.xj_02 = 'invalid';
        //     }
        //   } else {
        //     this.shej_02 = "广东省";
        //     this.shijCity = 'invalid',
        //     this.xj_02 = 'invalid'
        //     this.nameP = "invalid";
        //   }
        // }
        get_http("rdmxfxqtjjfxfldqfb", {
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          // name: this.nameP,
          // year:this.year,
          // id:this.curTypeId
        })
          .then(res => {
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击柱图下钻
      // openCounty(name){
      //   this.curCity = name;
      //   let obj={color:'#fefa82'}
      //   this.xflphBarParams.data.forEach((item,index)=>{
      //     if(this.curCity == item.name){
      //       this.countyBackShow = true
      //       this.$set(item, "itemStyle", obj)
      //     }else{
      //       this.$delete(item,"itemStyle");
      //     }
      //   })
      // },
      // 柱图返回全省
      // mapBack() {
      //   this.countyBackShow=false,
      //   this.curCity = '广东省';
      // },
      // 接口调试 
      getAPI(date){
        // 2.进京访信访量人数规模分布走势
        get_http("rdmxfxjjffbzs", { 
          day: `[${date.startDate},${date.endDate}]`,
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xi_02,
          })
          .then(res => {
            this.zsLineParams.map = res.data.map;
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.重复访次数分布
        get_http("rdmxfxjjfzffcsfb", {
          day: `[${date.startDate},${date.endDate}]`,
          // shej_02: 'invalid',
          // shij_02: 'invalid',
          // xi_02: 'invalid',
          }).then(res => {
            this.dqfbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        
        // 4.人数规模分布
        get_http("rdmxfxjjfrsgmfb", {
          day: `[${date.startDate},${date.endDate}]`,
          // shej_02: 'invalid',
          // shij_02: 'invalid',
          // xi_02: 'invalid',
          }).then(res => {
            this.peoplePieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.信访群体
        get_http("rdmxfxjjfxfqt", {
          day: `[${date.startDate},${date.endDate}]`,
          // shej_02: 'invalid',
          // shij_02: 'invalid',
          // xi_02: 'invalid',
          }).then(res => {
            this.rqnlBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6.信访目的分布
        get_http("rdmxfxjjfxfsq", {
          day: `[${date.startDate},${date.endDate}]`,
          // shej_02: 'invalid',
          // shij_02: 'invalid',
          // xi_02: 'invalid',
          }).then(res => {
            this.xfmdPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7.投诉问题分布
        get_http("rdmxfxjjftswtfb", {
          day: `[${date.startDate},${date.endDate}]`,
          // shej_02: 'invalid',
          // shij_02: 'invalid',
          // xi_02: 'invalid',
          })
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val);
      },
      curCity: function(newVal, oldVal) {
        this.shijCity = newVal;
        if(this.nameP === 'shij_02'||newVal==="广东省"){
          this.getColumnCharts(this.date)
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper-bg
  background url("../../../assets/wrapper-bg2.png") no-repeat center
  background-size 100% 100%
  width 100%
  height 100%
  .wrapper
    position relative
    top 0.45rem
    height calc(100% - 0.45rem)
    .content
      display flex
      flex-direction column
      height 100%
      padding .2rem .3rem
      box-sizing border-box
      .c-top
        flex 1
        display flex
        margin-bottom .3rem
        .c-t-left
          flex 1  
          display flex
          flex-direction column
          position relative
          .map-back
            position absolute
            top 30px
            right 60px
            width 30px
            height 30px
            background url("~@/assets/back-icon.png") no-repeat
            z-index 1000
            font-size 0.2rem
            color #fff
            cursor pointer
          .charts
            flex 1
        .flex-2
          flex 1.5
      .c-bottom
        flex 1
        display flex
        .c-b-left
          flex 1.2
          display flex
          flex-direction column
          position relative
          .charts
            flex 1
          p
            width 100%
            position absolute
            bottom .3rem
            right 0px
            color #fff
            font-size 20px
            text-align center
            font-weight bold

</style>
